<template>
  <!-- header -->
  <div class="header">
    <div v-if="step == 1">
      <ul class="header-button-left">
        <li style="color: black">Cancel</li>
      </ul>	

      <ul class="header-button-right" @click="step++">
        <li style="color: black">Next</li>
      </ul>

      <div style="font-size: 40px; text-align:center;">&#128396;</div>
    </div>

    <div v-if="step == 2">
      <ul class="header-button-right">
        <li @click="publish" style="color: black">Î∞úÌñâ</li>
      </ul>
    </div>
    
  </div>

  <!-- vuex ÏöîÏ≤≠Ìïú Í≤åÏãúÍ∏Ä Îçî Î≥¥Í∏∞ ÎÇ¥Ïö© Ï∂úÎ†• -->
  <!-- {{$store.state.post[0].name}} -->

  <!-- component -->
  <Container :post="post" :step="step" :url="url" @uploadText="content=$event"/>

  <!-- axios ÏöîÏ≤≠ÌïòÏó¨ ÍπÉÌóôÏÑúÎ≤ÑÏóêÏÑú json Í∞ÄÏ†∏Ïò§Í∏∞ -->
  <div v-if="step == 0">
    <button @click="more">
    <!-- <button @click="$store.dispatch('getData')">   -->
      Í≤åÏãúÍ∏Ä Îçî Î≥¥Í∏∞
    </button>
  </div>

  <!-- footer -->
  <div v-if="step == 0">
    <div class="footer">
      <ul class="footer-button-plus">
        <input @change="upload" type="file" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>

</template>

<script>
import Container from "./components/Container.vue";
import post from "./assets/data/post";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      content: '',
      url: '',
      step: 0,
      post: post,
      count: 0,
      selectFilter: '',
    };
  },
  components: { Container },
  methods: {
    more() {
      axios
        .get(`https://minjaehyun.github.io/vue/more${this.count++}.json`)
        .then((result) => {
          this.post.push(result.data);
        })
        .catch();
    },
    upload(e){
      let files = e.target.files;
      let file  = files[0];
      // console.log(file);
      let url   = URL.createObjectURL(file);
      // console.log(url);
      this.url  = url;
      this.step = 1;
    },
    publish(){
      // Í≤åÏãúÍ∏ÄÏùÑ ÏÑúÎ≤ÑÏóê Ï†ÄÏû•ÌïúÎã§
      // uploadPost ÎßåÎì§Í∏∞ (ÌòïÌÉúÎäî post.js Ï∞∏Ï°∞)
      var uploadPost = {
        name: "Min jae Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.url,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.content,
        filter: this.selectFilter,
      }
      this.post.unshift(uploadPost)
      // ÎßàÏßÄÎßâÏóê this.step ÏùÑ 0 ÏúºÎ°ú ÎßåÎì†Îã§
      this.step = 0;
    }
  },
  mounted() {
    this.emitter.on('clickBox', (a)=>{
      this.selectFilter = a
    })
  }
};
</script>

<style>
@import "assets/css/app.css";
</style>
